
import { defineComponent } from "vue";
import { mapMutations } from "vuex";

export default defineComponent({
  setup() {
    const toc = (process.env.VUE_APP_TOC as string)
      .split("\n")
      .map((c) => c.replace(/.md$/, ""));
    return { toc };
  },
  methods: {
    ...mapMutations(["setChapter"]),
  },
});
